/*!
Section 6: Footer
*/
footer a {
    text-decoration: underline;
}

footer {
    margin-top: 5rem;
    background: #0c3b78;
    padding: 2rem 0 4rem 0;
    font-size: .75rem
}

footer h2 {
    font-size: .75rem;
    color: #ffc600;
    text-transform: uppercase;
    font-weight: 700
}

footer .menu>li>a {
    padding: 0 .25rem 0 0
}

footer a,footer p {
    color: #fefefe;
    font-size: .75rem
}

footer a:hover, footer a:focus{
 color: #fff;
}

footer .footer-logo {
    margin-bottom: 2.5rem
}

@media only screen and (max-width: 40em) {
					footer ul {
								margin:0;
					} 
}

footer ul {
    list-style: none;
    margin-left: 0
}

footer ul li {
    margin-bottom: .75rem
}

